// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 
// 
import {
  STATUS_CODE,
  TITLE,
  MSG,
  BTN_TEXT,
  CONSTANTS_DEVICE_CONFIGURATION,
  VARIABLE,
  CONSTANTS_MATERIAL,
  FORM_CONFIG_DATE
} from '@/utils/constantsFile';
// ztree
import ZTree from '@/components/common/ZTree/ZTree';
// table
import VXETable from '@/components/common/Table';
// 
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 
// import moment from 'moment';
// 
import {
  deviceSelectDeviceByConditionAction,
  deviceGroupGetTreeAction,
  deviceWorkshopTypeGetTreeAction,
  getPageDeviceByDetectionDeviceCode
} from '@/api/management';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
// import { showErrorNotify } from '@/utils/notify';
// import moment from 'moment';
export default {
  components: {
    // 
    HeaderTitle,
    ZTree,
    HeadOperationItem,
    VXETable
  },
  props: {
    deviceCode: String
  },
  data() {
    // 
    const setValidateForm = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.formatterTableValidate(
          column,
          cellValue,
          this.extendPropertyVoList,
          resolve,
          reject
        );
      });
    };

    return {
      setValidateForm: setValidateForm,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      imageUrl: '',
      // 
      titleName: CONSTANTS_MATERIAL.ADD_MATERIAL,
      // 
      validRules: {},
      deviceName: '',
      // 
      HeadTitleUrl: '',
      // table
      HeadTitleName: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_LIST,
      // / ids
      searchIdArr: [],
      // table
      deviceChecks: [],
      tableName: 'device',
      // tab
      tabPosition: 'group',
      // 
      showModelItem: false,
      // 
      fatherNodeData: [],
      //  0 1
      showPage: 0,
      // 
      modelOptions: [],
      // 
      formParams: {
        model: null
      },
      // 
      formInline: {
        cnName: '',
        shortName: '',
        parameterName: '',
        isWarning: 'null'
      },
      // 
      rules: {
        name: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' }
        ]
      },
      //  1  2 3
      operaType: 1,
      // label
      formLabelWidth: '150px',
      // label
      formLabelWidthShort: '50px',
      // 
      rightMenu: [],
      // 
      form: {
        name: ''
      },
      // 
      titlename: '',
      dialogVisibleQrCode: false,
      // 
      dialogVisibleUpdate: false,
      // 
      dialogVisible: false,
      // ztree
      ztreeObj: {},
      // ztree
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree
      nodeData: [],
      // table
      tableLoading: false,
      // table
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 
          left: 'pager_left'
        }
      },
      // table
      tableDatas: [],
      // table
      tableColumns: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_NAME,
          align: 'center'
        },
        {
          field: 'deviceCode',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE,
          align: 'center'
        },
        {
          field: 'mode',
          title: '',
          align: 'center'
        },
        {
          field: 'typeName',
          title: '',
          align: 'center'
        },
        {
          field: 'groupName',
          title: '',
          align: 'center'
        },
        {
          field: 'groupName',
          title: '',
          align: 'center'
        }
      ],
      contrastData: []
    };
  },
  computed: {},
  created() {
    // 
    this.getTree();
    // 
    this.findList();
  },
  mounted() {
    // this.initExtendTable();
  },
  watch: {
    deviceData() {
    },
    // 
    tabPosition() {
      // 
      this.form = {
        name: ''
      };
      // ztree
      this.ztreeSelectedNode = {
        id: '-1',
        pId: '-1'
      };

      this.getTree();
    }
  },
  methods: {
    /**
     * @description: 
     * @param 
     * @return:
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', this.GLOBAL.VARIABLE.ZERO_NUMBER);
    },
    tableChooseCheck() {},
    /**
     * @description: ztree
     * @param event 
     * @param nodes 
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // nameid
      this.searchIdArr = [];
      var nameUrl = [];
      nameUrl.push(nodes.name);
      var node = nodes.getParentNode();
      while (node) {
        nameUrl.push(node.name);
        node = node.getParentNode();
      }
      this.HeadTitleUrl = nameUrl.reverse().join(' / ');
      this.searchIdArr = this.getChildren(this.searchIdArr, nodes);

      // 
      this.ztreeSelectedNode = nodes;
      // ztree
      this.ztreeObj = obj;
      // 
      this.tablePage.currentPage = 1;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
    },
    /**
     * @description: table
     * @param currentPage 
     * @param pageSize 
     * @return: 
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // table
      this.tablePage.currentPage = currentPage;
      // table
      this.tablePage.pageSize = pageSize;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param val
     * @return: 
     * @author: ZJM
     */
    async findList() {
      try {
        // table
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize,
          deviceStateList: [1, 3, 0]
        };
        if (this.searchIdArr.length > 0) {
          if (this.tabPosition === 'group') {
            params.groupIdList = this.searchIdArr;
          } else if (this.tabPosition === 'type') {
            params.typeIdList = this.searchIdArr;
          }
        }

        const res = await deviceSelectDeviceByConditionAction(params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
          this.toFindCheck(res.rows);
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async getTree() {
      if (this.tabPosition === 'group') {
        // 
        try {
          const res = await deviceGroupGetTreeAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.nodeData = res.rows;
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else if (this.tabPosition === 'type') {
        // 
        try {
          const res = await deviceWorkshopTypeGetTreeAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.nodeData = res.rows;
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    },
    /**
     * @description: 
     * @param data 
     * @return: 
     * @author: ZJM
     */
    async toFindCheck(data) {
      try {
        const res = await getPageDeviceByDetectionDeviceCode({
          current: 1,
          size: 100000,
          detectionDeviceCode: this.deviceCode
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.contrastData = res.rows;
          for (var i in data) {
            for (var j in res.rows) {
              if (data[i].deviceCode === res.rows[j].deviceCode) {
                this.$refs.xGrid.toggleCheckboxRow(data[i]);
              }
            }
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    submitEdit() {
      const checkedData = this.$refs.xGrid.getCheckboxRecords();
      if (this.isFunc) {
        this.$emit(
          'saveRelatedDevice',
          checkedData,
          this.tableDatas,
          this.contrastData
        );
      } else {
        this.$emit('saveRelatedDevice', checkedData, this.tableDatas);
      }
    }
    // /**
    //  * @description: 
    //  * @param 
    //  * @return: 
    //  * @author: ZJM
    //  */
    // cancelBtnClick() {
    //   this.$emit('hiddenRelated');
    // }
    // /**
    //  * @description: 
    //  * @param val 0index
    //  * @return:
    //  * @author: ZJM
    //  */
    // goBack(val) {
    //   // 
    //   this.showPage = val;
    //   // 
    //   this.findList();
    // }
  }
};
